body {
  background-color: #172442;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.cat-info {
  max-width: 1200px;
  gap: 3px;
  margin: -39px auto;
  display: flex;
}

.wrap-foto {
  min-width: 650px;
  min-height: 550px;
  background-color: rgba(255, 255, 255, .15);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 20px 0 0 20px;
}

.wrap-description {
  color: #fff;
  background-color: rgba(255, 255, 255, .15);
  border-radius: 0 20px 20px 0;
  padding: 30px;
}

.title {
  color: #d5339f;
  font-size: 32px;
}

.description, .temperament {
  font-size: 18px;
}

div.ss-main {
  width: 300px;
  color: #d5339f;
  background-color: #172442;
  border: 1px solid #d5339f;
  margin: 30px 0 0 20px;
  padding: 8px;
  font-size: 18px;
  font-weight: 700;
}

div.ss-main .ss-arrow path {
  stroke: #d5339f;
}

div.ss-content {
  font-size: 18px;
}

div.ss-content .ss-search input {
  border: 1px solid #172442;
}

div.ss-content .ss-search input:focus {
  box-shadow: 0 0 5px rgba(23, 36, 66, .72);
}

div.ss-content .ss-list .ss-option:not(.ss-disabled).ss-selected {
  background-color: #172442;
}

.ss-content .ss-list .ss-option:hover {
  color: #d5339f;
  background-color: #fff;
}

.ss-main:focus {
  box-shadow: 0 0 5px #ee7dec;
}

.error {
  display: none;
}

.loader {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(64, 14, 50, .373);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #d5339f;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

